.blog-post {
  margin-bottom: 30px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 20px;
}

/* üîπ Í∞§Îü¨Î¶¨ Ïä§ÌÉÄÏùº */
.post-gallery {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.post-gallery img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 5px;
  transition: transform 0.3s ease-in-out;
}

.post-gallery img:hover {
  transform: scale(1.1);
}
